.btn-primary{

    margin-top: 10px;
    background-color: blue;
}
.btn-group .btn {
    border-radius: 50%;
    width: 35px; 
    height: 35px; 
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px; 
    padding: 0; 
    transition: background-color 0.3s ease, transform 0.2s ease; 
    margin-bottom: 10px;
  }
  
  .btn-group .btn:hover {
    transform: scale(1.1); 
  }
  
  .btn-group .btn:focus {
    outline: none; 
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5); 
  }
  .ThemeText{

color: rgb(255, 0, 0);
margin-top: 5px;
margin-right: 5px;

  }
  .alert-container {
    height: 50px; /* Reserve space for the alert */
    display: flex;
    align-items: center; /* Center the alert vertically */
    justify-content: center; /* Center the alert horizontally, if needed */
    margin-bottom: 10px; /* Add spacing between the alert and other components */
  }
  
  .alert-container .alert {
    margin: 0; /* Remove default margins from the alert */
  }
  .accordion-item {
    border: none; /* Removes the outline of the accordion item */
  }
  
  .accordion-header {
    border-top: none; /* Removes the top border */
  }
  
  .accordion-item:last-child .accordion-header {
    border-bottom: none; /* Removes the bottom border for the last accordion item */
  }
  
  .accordion-button {
    box-shadow: none; /* Removes the default box shadow of the button */
  }
  
  .accordion-button:focus {
    outline: none; /* Removes the focus outline */
  }
  
  .accordion-body {
    border-top: none; /* Ensures no top border inside the body */
  }
  
  .container {
    border: none; /* Ensures no border is applied to the container */
    box-shadow: none; /* Removes shadow effects, if any */
  }
  
  div[style*="border"] {
    border: none !important; /* Overrides inline border styles, if present */
  }
  